.profitItems {
  margin-left: -15px;
  margin-right: -15px;
}

.profitItems::after {
  display: block;
  content: "";
  clear: both;
}

.profitItem {
  float: left;
}

.humans .items {
  margin-left: -15px;
  margin-right: -15px;
  font-size: 0;
  text-align: center;
}

.humans .items .item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  display: inline-block;
  vertical-align: top;
  font-size: 16px;
}

@media screen and (max-width: 400px) {
  .profitItems {
    margin-left: -5px;
    margin-right: -5px;
  }

  .humans .items {
    margin-left: -5px;
    margin-right: -5px;
  }

  .humans .items .item {
    margin-left: 5px;
    margin-right: 5px;
  }
}